<template>
  <horizontal>
    <b-overlay :show="showLoader" rounded="sm">
    <div class="row justify-content-center">
      <div class="col-md-8 col-lg-6 col-xl-5">
        <div class="card">
          <div class="card-body p-4">
            <div class="text-center w-75 m-auto">
              <div class="auth-logo">
                <router-link to="/" class="logo logo-dark text-center">
                  <span class="logo-lg">
                    <img src="@/assets/images/logo.svg" alt height="22" />
                  </span>
                </router-link>
                <router-link to="/" class="logo logo-light text-center">
                  <span class="logo-lg">
                    <img src="@/assets/images/logo-light.png" alt height="22" />
                  </span>
                </router-link>
              </div>
              <p class="text-muted mb-4 mt-3">
                A verification code has been sent to your email. Please enter the code
              </p>
            </div>

            <form @submit.prevent="ResetPinConfirm" >
              <AlertMessage :variant="dangerSuccess" :message="responseMessage" v-if="responseMessage"/>
              <div class="form-group mb-3">
                <label for="otpvalue">Enter verification code</label>
                <input
                    class="form-control"
                    type="text"
                    id="otpvalue"
                    placeholder="Enter otp pin"
                    v-model="otp"
                    v-validate="'required'"
                    :class="{'is-invalid': vErrors.has('otp') }"
                    name="otp"
                />
                <span v-show="vErrors.has('otp')" class="help text-danger">{{ vErrors.first('otp') }}</span>
              </div>
              <div class="form-group mb-0 text-center">
                <router-link to="/">
                  <button class="btn btn-outline-primary"  style="float: left;"  type="submit">
                    Back to dashbard
                  </button>
                </router-link>
                <button class="btn btn-primary pull-right" style="float: right;" id="bkcolor" type="submit">
                  Confirm otp
                </button>
              </div>
            </form>

          </div>
          <!-- end card-body -->
        </div>
        <!-- end card -->

        <!-- end row -->
      </div>
      <!-- end col -->
    </div>
    </b-overlay>
  </horizontal>
</template>

<script>
import customerInfo from "../../protos/Customer_grpc_web_pb";
import constant from "../../components/constant";
import horizontal from "../layouts/horizontal";
//import SweetAlertWrapper from "../../sweetAlert";
import AlertMessage from "../../components/shared/AlertMessage";
export default {
  data(){
    return{
      responseMessage:'',
      otp:'',
      showLoader: false,
      dangerSuccess:'',
      clients:{}
    }
  },
  components:{
    horizontal,
    AlertMessage
  },
  methods:{
    ResetPinConfirm(){
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.showLoader = true;
          this.responseMessage = '';
          this.emailaddress = this.$store.getters.getUserEmailAddress.emailAddress;
          let request = new customerInfo.VerifyOTPRequestMessage();

          request.setOtpvalue(this.otp);
          request.setCustomerid(this.$store.getters.getUserInfo.emailAddress);
          request.setPersonalbusinessvalue("2");
          request.setLanguageid("en")
          this.clients.verifyTransOTPNow(request, {}, (err,res) =>{
            this.showLoader = false;
            if(res.getIssuccess()){
              this.$store.dispatch('PasswordResetOtpvalue', {
                otpvalue : this.otp
              })
              this.$router.push('/business/pinreset').catch(() => { })
            }
            else {
              this.dangerSuccess = 'danger';
              this.responseMessage = res.getMessage();
            }
          });
          return;
        }
      });
    }
  },
  mounted() {
    this.clients = new customerInfo.CustomerAccountClient(constant.URL,null,null);
  },
  created() {
   // this.sweetAlert = new SweetAlertWrapper()
  }
}
</script>

<style scoped>
button:hover {
  background: #0078D4;
}
</style>